.App {
  text-align: center;
}

#container {
  margin-top: 10%;
}

#container, #components, #type-correct, #results {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#words, #focus {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 100%;
}

.background-word {
  display:flex;
  margin-left: .5rem;
  margin-right: .5rem;
  font-size: 1.2rem;
  color: grey;
}

#word {
  background: lightgrey;
  padding: .4rem;
}

#type {
  text-align: center;
  width: 10rem;
  margin-top: 1rem;
}

#results {
  display: none;
  border-top: inset;
  border-color: black;
  border-width: .2rem;
  padding: .4rem;
  margin-top: 1rem;
  border-radius: .2rem;
}

#seconds {
  font-size: .8rem;
  display: none;
}

footer {
  margin-top: 1rem;
  font-size: 0.6rem;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
